export function displayDate(str) {
  const days = ["", "", "", "", "", "", ""];
  const months = [
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
  ];
  // eslint-disable-next-line
  const i = str.replace(/(\d+)\.(\d+)\.(\d+)/g, `20$3-$2-$1`);
  const d = new Date(i);

  return (
    d.getDate() +
    " " +
    months[d.getMonth()] +
    " " +
    d.getFullYear() +
    ", " +
    days[d.getDay()]
  );
}
